import React, { Component, PropTypes } from 'react';
import emptyFunction from 'fbjs/lib/emptyFunction';
import s from './App.scss';

import Feedback from '../Feedback';
import Footer from '../Footer';

class App extends Component {
  constructor(){
    super();
    this.state = {posY: 0, posX:0};
  }
  static propTypes = {
    context: PropTypes.shape({
      insertCss: PropTypes.func,
      onSetTitle: PropTypes.func,
      onSetMeta: PropTypes.func,
      onPageNotFound: PropTypes.func,
    }),
    children: PropTypes.element.isRequired,
    error: PropTypes.object,
  };

  static childContextTypes = {
    insertCss: PropTypes.func.isRequired,
    onSetTitle: PropTypes.func.isRequired,
    onSetMeta: PropTypes.func.isRequired,
    onPageNotFound: PropTypes.func.isRequired,
  };

  getChildContext() {
    const context = this.props.context;
    return {
      insertCss: context.insertCss || emptyFunction,
      onSetTitle: context.onSetTitle || emptyFunction,
      onSetMeta: context.onSetMeta || emptyFunction,
      onPageNotFound: context.onPageNotFound || emptyFunction,
    };
  }
  mover(e){
    if(this.props.children.props.is == 'init'){
      var x = ((window.innerWidth/2) - e.pageX) *0.005;
      var y = ((window.innerHeight/2) - e.pageY) *0.005;
      this.setState({posY:y,posX:x})
    }
  }
  componentWillMount() {
    this.removeCss = this.props.context.insertCss(s);
  }

  componentWillUnmount() {
    this.removeCss();
  }



  render() {
  
    return !this.props.error ? (
      <div onMouseMove={this.mover.bind(this)}>


        {this.props.children}


        <div className={s.fullvideo} style={{backgroundImage: 'url(/fondo.jpg)',transform: 'translateX(-'+(50-this.state.posX)+'%) translateY(-'+(50 - this.state.posY)+'%)'}} >
        </div>
      </div>
    ) : this.props.children;
  }

}

export default App;



