import React from 'react';
import Router from 'react-routing/src/Router';
import fetch from './core/fetch';
import App from './components/App';
import ContentPage from './components/ContentPage';
import InitPage from './components/InitPage';
import LoginPage from './components/LoginPage';
import RegisterPage from './components/RegisterPage';
import NotFoundPage from './components/NotFoundPage';
import ErrorPage from './components/ErrorPage';

const router = new Router(on => {
  on('*', async (state, next) => {
    const component = await next();
    return component && <App context={state.context}>{component}</App>;
  });
  on('/', async () => <InitPage is="init" as="0"/>);
  on('/inicio', async () => <InitPage is="inicio" as="1" isnum="0"/>);
  on('/contacto', async () => <InitPage is="contacto" as="1" isnum="2"/>);
  on('/clientes', async () => <InitPage is="clientes" as="1" isnum="3"/>);
  on('/servicios', async () => <InitPage is="servicios" as="1" isnum="1"/>);
  on('/servicios/:id', async (req) => <InitPage is="servicios" as="1" servicio={req.params.id}/>);




});

export default router;



