  constructor(props) {
   super(props);
   this.state = {margin:0,no_menu: true,open_menu: 0, height: 600, on_servicio: 0, mouse_on: false, sms: false,modY:0, elements: ['inicio','servicio','contacto'],time: '',scrolling: false,sales_state:0}
 }

  static contextTypes = {
    onSetTitle: PropTypes.func.isRequired,
  };

  componentWillMount() {
    this.context.onSetTitle(title);
  }


  componentDidMount(){
     window.scrolling = false;


    scroller.scrollTo(this.props.is, true, 0, 0);
    if (typeof document !== 'undefined') {
      this.time = 0;
      this.modY = 0;
      document.addEventListener('scroll', this.scrollHandler.bind(this));
      document.addEventListener('scroll', this.scrolling.bind(this));
      document.addEventListener('keydown', this._keyPress.bind(this));
    }




  }
  handleResize(){

  }
  componentDidUpdate(){

    if(!window['scrolling']){

      scroller.scrollTo(this.props.is, true, 400, 0);
    }




  }

  scrolling(){
    var este = this;
      window['scrolling'] = true;

      clearTimeout(window['timer']);

      window['timer'] = setTimeout(function(){
        if(window.scrollY == window['lastY']){

          if(window['lastY'] != window['lastSetY']){
            window['lastSetY'] = window['lastY'];

          }
          window['scrolling'] =false;
          clearTimeout(window['timer']);

        }
      },200);


      window['lastY'] = window.scrollY;

  }

  scrollHandler(){

    return false;
    window.clearTimeout(window['tiempo_var']);
    var este = this;

    if(this.props.servicio != undefined){

      return false;
    }

    if(scrollSpy.currentPositionY() == 0){
      return false;
    }

    window['tiempo_var']  = setTimeout(function(){

      if(window.scrolling){
        return false;
      }
      if(scrollSpy.currentPositionY() > (window.innerHeight*0.1) && scrollSpy.currentPositionY() < (window.innerHeight*1.1) ){
        Location.push({
          ...(parsePath('/servicios')),
          state: this.props  ,
        });

      } else if(scrollSpy.currentPositionY() > (window.innerHeight*1.1)){
        Location.push({
          ...(parsePath('/contacto')),
          state: this.props  ,
        });

      } else if(scrollSpy.currentPositionY() < (window.innerHeight*0.1)) {
        if(scrollSpy.currentPositionY() == 0){
          return false;
        }

        Location.push({
          ...(parsePath('/inicio')),
          state: this.props ,
        });
      } else if((window.innerHeight) < scrollSpy.currentPositionY()) {
        este.handleResize()
      }

      window.clearTimeout(window['tiempo_var']);
    },500);

  }

  componentWillUnmount() {
    scrollSpy.unmount();
    scroller.unmount();
    window.removeEventListener('resize', this.handleResize);
  }

  toggleMenu(event){
    console.log(scroller)
    if(this.state.open_menu == 0){
      this.setState({open_menu: 1})
    } else {
      this.setState({open_menu: 0})
    }

  }

  onServicio(servicio,e){
    console.log(e);
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();

    this.setState({mouse_on:true, on_servicio: servicio})
  }

  offServicio(e){
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();
    this.setState({mouse_on:false})
  }

  callSms(){
    this.setState({sms: true})
  }
  _keyPress(e){
    var este = this;

  }
